@import "angled-edges";


$orange:#FFA22B;
$accent:#5EA4F7;
$yellow:#FED766;
$black:#515151;
$hero-black:#383F41;

$angle-height:60;

.container {
    max-width: 1140px
}
body {
    font-size: 1.4rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    color: $black;
     -webkit-font-smoothing: antialiased;
}
@media screen and (min-width: 550px) {
    body {
        font-size: 1.4rem
    }
}
.slate {
    color: #252a30
}
.gunmetal {
    color: #524e4d
}
.gray {
    color: #93a4aa
}
.yellow {
    color: $yellow;
}
.white {
    color: #fff
}

.black {
    color: $black;
}

.accent{
    color: $accent;
}

.orange{
    color:$orange;
}

.bg-orange{
    background-color: $orange;
}


.divider {
    width: 60px;
    height: 4px;
    background-color: #FED766;
    margin: 32px auto
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    color: #252a30
}
h2.intro {
    font-size: 4.2rem;
    /*margin: 6rem 0 1.8rem;*/
    line-height: 1.4;
    font-weight: 700;
    text-align: center;
    letter-spacing: 2px;
}
@media screen and (min-width: 750px) {
    h2.intro {
        font-size: 5.2rem;
        /*margin: 10rem 0 2.0rem*/
    }
}
p.statement {
    font-size: 1.6rem;
    text-align: center
}
@media screen and (min-width: 750px) {
    p.statement {
        font-size: 2.0rem
    }
}

a {    
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    text-decoration: none;
    

    &.accent{
        border-bottom: 1px dotted $accent;

        &:hover {
            /* #4288DC */
            color: $orange;
            border-bottom: 1px dotted $orange;
        }
    }

    &.white{
        border-bottom: 1px dotted #fff;
        
        &:hover {
            color: $accent;
            border-bottom: 1px dotted $accent;
        }            
    }  

}

a.button:hover {
    border: 2px solid #444
}




section {
    padding: 30px 0;
}

