@media screen and (min-width: 1000px) {
    section {
        padding: 0 0 60px
    }


}

*/

.section {
        margin: 60px 0;
    }

.section:last-child{
    margin: 60px 0 0; 
}

#fullscreen-hero {
    height: 100vh;
    position: relative;
    text-align: center;
    width: 100%;
    min-height: 350px;
    background-color: #252a30;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;

    @include angled-edge("outside bottom", 'lower left', $hero-black,$angle-height);
}

#fullscreen-hero:before,
{
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%
}
.home-hero {
    position: relative;
    background-image: none
}
@media screen and (min-width: 550px) {
    .home-hero {
        background-image: url(./images/hero-bg.jpg)
    }
}
.home-hero:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    pointer-events: none;
    background-image: -webkit-gradient(linear, left top, right top, from(rgba(49, 61, 65, 0.6)), to(rgba(49, 61, 65, 0.6)));
    background-image: linear-gradient(90deg, rgba(49, 61, 65, 0.6), rgba(49, 61, 65, 0.6))
}
.hero-container {
    display: table;
    margin: 0 auto;
    padding: 0 20px;
    height: 100%
}
@media screen and (min-width: 550px) {
    .hero-container {
        padding: 0 40px
    }
}
.hero-content {
    display: table-cell;
    position: relative;
    z-index: 10;
    text-align: center;
    vertical-align: middle
}
.hero-content h1 {
    font-size: 2.3rem;
    color: #fff;
    margin-bottom: 0rem
}
@media screen and (min-width: 750px) {
    .hero-content h1 {
        font-size: 10rem
    }
}
@media screen and (min-width: 1000px) {
    .hero-content h1 {
        font-size: 11rem
    }
}
.hero-content a.button-ghost {
    margin: 3.6rem 0 0;
    padding: 1.0rem 2.6rem;
    font-size: 1.1rem;
    border: 2px solid #FED766
}
.hero-content a.button-ghost:hover {
    padding: 1.0rem 3.4rem
}
@media screen and (min-width: 550px) {
    .hero-content a.button-ghost {
        margin: 6.0rem 0 0;
        padding: 1.4rem 3.2rem;
        font-size: 1.4rem;
        border: 2px solid #FED766
    }
    .hero-content a.button-ghost:hover {
        padding: 1.4rem 4.0rem
    }
}
.learn-more {
    bottom: 2.5%;
    display: inline-block;
    font-size: 1.3rem;
    left: 50%;
    padding: 20px;
    position: absolute;
    white-space: nowrap;
    z-index: 2;
    text-decoration: none;
    -webkit-transition: color 0.2s ease-out;
    transition: color 0.2s ease-out;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%)
}
@media screen and (min-width: 750px) {
    .learn-more {
        font-size: 1.7rem
    }
}
.learn-more a {
    color: rgba(255, 255, 255, 0.7);
    text-decoration: none;
    border-bottom: none
}
.learn-more a:hover {
    color: rgba(255, 255, 255, 0.9)
}
.home-logo img {
    top: 2.5%;
    display: inline-block;
    left: 50%;
    width: 50px;
    height: auto;
    padding: 20px;
    position: absolute;
    white-space: nowrap;
    z-index: 2;
    text-decoration: none;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%)
}
@media screen and (min-width: 750px) {
    .home-logo img {
        width: 48px
    }
}
@media screen and (min-width: 1000px) {
    .home-logo img {
        width: 60px
    }
}


#services,#testimonials {
    text-align: center;
    margin: 0 auto;
    @include angled-edge("outside bottom", 'lower left', $orange,$angle-height);
    @include angled-edge("outside top", 'upper left', $orange,$angle-height);
}
#services .expertise-block {
    margin: 4.0rem 0 1.0rem
}
@media screen and (min-width: 750px) {
    #services .expertise-block {
        margin: 4.0rem 0 0
    }
}
#services .expertise-block img {
    width: auto;
    height: 14.0rem;
    max-width: 100%
}
#services .expertise-block h3.title {
    margin: 1.8rem 0;
    text-transform: capitalize;
}
#services .expertise-block p {
    margin-bottom: 0;
    line-height: 1.8;
    font-size: 1.4rem;
    padding: 0 10%;
}
#services i.fa {
    font-size: 5.0rem
}



#hero-tagline{
    font-size: 5.7rem;
    color:$orange;
    font-weight: 700;
}



@media screen and (min-width: 890px) {
    #services .expertise-block p {
        margin-bottom: 0;
        line-height: 1.8;
        font-size: 1.4rem;
        padding: 0 2%;
    }
}




#footer, footer p,#copyright a,.sm-nav li a,.glyphicon{color: white;}

#copyright {margin-top:20px;font-size:1.3em}

#footer{  
  background: $hero-black;
  @include angled-edge("outside top", 'upper left', $hero-black,$angle-height);
}

#footer i{
    font-size: 4em;
    color: white;
    width:100px;
    line-height:100px;
  border-radius:50%;
  transition: color .5s,background .5s;
  
}

#footer i.fa:hover{
    color: $orange;    
}

.circle-parent{position:relative; display:inline-block;z-index: 1;}

.circle{
  width:0px;
  height:0px;
  position:absolute;
  top:50%;
  left:50%;
  border-radius:50%;
  z-index:-1;
  background:white;
  transition: width .3s, height .3s, top .3s, left .3s;
}




















































